.search {
  /* border: 3px dashed tomato; */
  display: -ms-flexbox;
  display: flex;
  padding: 0px 20px;
  width: auto;
}

.search input {
  -ms-flex: 1 1;
      flex: 1 1;
  margin-right: 20px;
  width: 100%;
  border: 1px solid #bec3c7;
  border-radius: 20px;
  padding: 5px 10px;
}

@media only screen and (max-width: 700px) {
  .search {
    padding: 0;
  }
  .search input {
    margin: 0;
  }
}



