

.note {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  padding: 0px 20px 0px 30px;
}

textarea {
  font-family: arial, cursive, sans-serif;
  border: none;
  outline: none;
  resize: none;
  overflow: auto;
  -webkit-transition: .1s linear;
  -o-transition: .1s linear;
  transition: .1s linear;
}

.title {
  -ms-flex-item-align: start;
      align-self: flex-start;
  min-width: 90%;
  font-size: 1.25em;
  padding: 15px 15px 8px 15px;
  border-bottom: 1px solid #ecf0f1;

  /* overflow-x: auto;
  overflow-y: hidden;
  box-sizing: border-box;
  width: auto; */
}

.title:focus {
  border-bottom: 1px solid #bec3c7;
}

.title-meta {
  text-align: right;
  /* position: absolute;
  right: 0;
  top: 92px; */
}

.title-meta p, .unsaved {
  padding: 0;
  margin: 0;
  display: inline-block;
  margin-right: 35px;
  font-size: 0.8em;
  color: #95a5a5;
  letter-spacing: 2px;
}

.title-meta .unsaved {
  color: rgba(53, 152, 219, 1);
  padding: 0px 5px;
  font-weight: 900;
}

.body {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 0px 30px 0px 0px;
}



